.bgColorAbout {
    width: 100vw;
}
.aboutPage {
    width: 90%;
    margin: 0 auto;

    .largeTxt {
        display: flex;
        flex-direction: column;
        justify-content: left;

        p {
            font-family: "vision";
            font-weight: 900;
            font-size: 23vw;
            color: #f2f4f6;
            line-height: 17vw;
            letter-spacing: -2rem;
            margin: 0;
        }
    }

    .smallTxt {
        display: flex;
        justify-content: space-around;
        margin: 10rem 0 0 0;

        .threedCube {
            width: 40%;
            height: auto;
            margin: 0;
            display: flex;
            justify-content: center;
            align-items: center;

            img {
                width: 100%;

                object-fit: cover;
            }
        }

        .text_thing {
            width: 40%;
            height: auto;
            font-size: 2vw;
            color: white;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: flex-start;

            font-family: "Louis George Cafe Light";
            line-height: 3vw;
            margin: 0;

            h1 {
                font-family: "vision";
                font-weight: 900;
                font-size: 5vw;
                color: #f2f4f6;
                margin: 5rem 0;
            }
        }
    }
}

.floorImg {
    width: 80%;

    .frontImg {
        width: 50rem;
        height: 50rem;

        img {
            width: 100%;
            height: auto;
            object-fit: cover;
        }
    }
}
