.Contact {
    width: 100%;
    height: 100vh;
    background: #ffb549;

    h1 {
        font-family: "Louis George Cafe Light";
        font-size: 36px;
        margin-top: 10vh;
        text-align: center;
    }
    .positioner {
        .group1 {
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;

            p {
                position: absolute;
                top: 70%;
                left: 40%;
                z-index: 100;
                font-family: "Louis George Cafe Light";
                font-style: italic;
                font-weight: 400;
                font-size: 28px;
                color: #000000;
                transform: translate(-50%, -50%);
            }

            img {
                object-fit: cover;
            }
        }
        .group2 {
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;

            p {
                width: 270px;
                position: absolute;
                top: 70%;
                left: 45%;
                z-index: 100;
                font-family: "Louis George Cafe Light";
                font-style: italic;
                font-weight: 400;
                font-size: 28px;
                color: #000000;
                transform: translate(-50%, -50%);
            }
            p:nth-child(2) {
                top: 60%;
            }

            img {
                object-fit: cover;
            }
        }

        // .group1,
        // .group2 {
        //     img:hover {
        //         filter: brightness(1.1);
        //         transition: all 0.5s;
        //     }
        // }
    }
}
