* {
    margin: 0 auto;
    padding: 0;
    box-sizing: border-box;
}

@font-face {
    font-family: "vision";
    src: url("./Assets/font/vision/Vision-Black.ttf");
}
@font-face {
    font-family: "vision";
    src: url("./Assets/font/vision/Vision-Bold.ttf");
}
@font-face {
    font-family: "vision";
    src: url("./Assets/font/vision/Vision-Regular.ttf");
}
@font-face {
    font-family: "vision";
    src: url("./Assets/font/vision/Vision-Heavy.ttf");
}
@font-face {
    font-family: "Louis George Cafe Light";
    src: url("./Assets/font/LouisCafe/Louis\ George\ Cafe.ttf");
}
@font-face {
    font-family: "Louis George Cafe Light";
    src: url("./Assets/font/LouisCafe/Louis\ George\ Cafe\ Light.ttf");
}
@font-face {
    font-family: "Louis George Cafe";
    src: url("./Assets/font/LouisCafe/Louis\ George\ Cafe\ Light.ttf");
}
@font-face {
    font-family: "Louis George";
    src: url("./Assets/font/LouisCafe/Louis\ George\ Cafe\ Light.ttf");
}
@font-face {
    font-family: "Dobby Duke";
    src: url("./Assets/font/Dobby/Dobby\ Duke.otf");
}

html {
    font-size: 62.5%;
    scroll-behavior: smooth;
}

a,
a:hover,
a:active,
a:visited {
    text-decoration: none;
    margin: 0;
    color: white;
}

ul {
    list-style: none;
    margin: 0;
}

li {
    margin: 0;
}

hr {
    border-bottom: 0;
    border-left: 0;
    border-right: 0;
    margin: 0;
}

.App {
    position: absolute;
    width: 100%;
    height: auto;

    z-index: -1000;
    padding: 0;
    background-color: rgba(75, 25, 245, 1);
}

.shoe {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(50%, 50%);
    // background-color: white;
    width: 50vw;
    height: 50vw;
    z-index: 1000;
}

.BgPurple {
    background-color: rgb(136, 0, 255);
}

.BgBlack {
    background-color: rgb(39, 39, 39);
}

.header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 0 5rem 0;
    background-color: transparent;
    z-index: 1000;

    @keyframes rotation {
        from {
            transform: rotate(0deg);
        }
        to {
            transform: rotate(359deg);
        }
    }

    .logo {
        position: relative;
        width: 10vw;
        height: 10vw;
        animation: rotation 100s infinite linear;
        background-image: url("./Assets/img/3dReplacement_2.png");
        background-position: center;
        background-size: 300%;
        background-repeat: no-repeat;

        img {
            position: absolute;
            top: 0;
            left: 0;
            transform: translate(-50%, -50%);
            width: 400%;
            height: auto;
            object-fit: cover;
            z-index: -10;
        }
    }

    div {
        margin: 0;
        padding: 0 5rem;
    }

    ul {
        display: flex;
        justify-content: space-between;
        align-items: center;

        li {
            margin: 0 2rem;
            font-size: 1.6rem;
            color: #f2f4f6;
            width: 10rem;
            height: 5rem;

            display: flex;
            align-items: center;
            justify-content: center;
        }

        li :hover {
            width: 10rem;
            height: 5rem;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: #f2f4f6;
            color: #000;
            transition: all 0.3s;
            border-radius: 5rem;
        }
    }
}

.largeTxt {
    div {
        display: flex;
        justify-content: center;
        align-items: center;

        ul {
            font-family: "vision";
            font-weight: 900;
            font-size: 23vw;
            color: #f2f4f6;
            line-height: 17vw;
            letter-spacing: -2rem;

            li:nth-child(1) {
                color: black;
            }
        }
        .img_icon_1,
        .img_icon_2 {
            position: relative;
            width: 30vw;
            height: 30vw;
            background-color: transparent;
        }
    }
}

.intro {
    display: flex;
    flex-direction: column;
    justify-content: right;
    align-items: flex-end;
    width: 100%;
    height: auto;
    padding: 5vh 0;

    .txt-small {
        width: 40%;
        font-size: 2vw;
        font-family: "Roboto";
        color: white;
        font-family: "Louis George Cafe Light";
        line-height: 3vw;
        margin: 0 30vw 0 0;
    }
    .movie {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 40vw;
        height: 30vw;
        background-color: black;
        margin: 10% 10% 0 0;
        border: 10px solid white;
        transform: rotate(-10deg);

        .react-player {
            display: flex;
            justify-content: center;
            align-items: center;
            object-fit: cover;
            height: 100% !important;

            video {
                object-fit: cover;
            }
            p {
                font-size: 30px;
                color: white;
                z-index: 100;
            }
        }
    }

    .txt-movie {
        width: 40vw;
        transform: rotate(-10deg);
        font-size: 3rem;
        font-family: "Louis George Cafe Light";
        font-weight: 400;
        color: white;
        margin: 0 3rem 0 0;
    }
}

.works {
    padding: 0 0 0 5rem;

    .col_1 {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 40rem;

        .img {
            width: 35vw;
            height: 45vw;
            background-color: #fff;
            margin: 0;

            display: flex;
            justify-content: center;
            align-items: center;

            border: 3px solid white;

            img {
                width: 100%;
                height: 100%;

                object-fit: cover;
            }
        }

        ul {
            color: white;
            font-family: "Louis George Cafe Light";
            width: 50%;

            li {
                margin-bottom: 1rem;
            }
            li:nth-child(1) {
                font-size: 2.5vw;
            }
            li:nth-child(2) {
                font-size: 5.5vw;
            }
            li:nth-child(3) {
                width: 80%;
                font-size: 1.5vw;
            }
        }
    }

    .col {
        position: relative;
        width: 100%;
        height: auto;

        display: flex;
        justify-content: right;

        .wrapper {
            width: 50%;
            height: auto;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-items: left;
            margin: 0;

            .img:hover {
                img {
                    filter: brightness(1.2);
                }
            }
            .img {
                width: 100%;
                height: auto;

                display: flex;
                justify-content: left;
                align-items: flex-start;

                margin: 0;
                overflow: hidden;

                img {
                    width: 80%;
                    height: 100%;
                    object-fit: cover;
                    margin: 0;
                    border: 0.5rem solid white;
                }

                .right_wing {
                    font-family: "Louis George Cafe Light";
                    color: white;
                    font-size: 2vw;
                    margin: 5rem;
                    writing-mode: vertical-rl;
                }
            }

            .bot_title {
                font-family: "Dobby Duke";
                font-size: 4vw;
                color: #f2f4f6;
                margin: 2vw 2vw;

                p {
                    margin: 0;
                    line-height: 100%;
                }

                p:nth-child(1) {
                    color: black;
                }
            }
        }
    }
    .col_2 {
        justify-content: right;
        margin: 5rem 0;

        .wrapper {
            position: relative;
            width: 45vw;

            .img {
                display: flex;
                justify-content: left;
                align-items: flex-end;

                img {
                    position: absolute;
                    width: 40vw;
                    height: 30vw;
                }

                .right_wing {
                    font-family: "Louis George Cafe Light";
                    color: white;
                    font-size: 2vw;
                    margin: 0 0 0 8vw;
                    writing-mode: vertical-rl;
                    z-index: 1;
                    width: 35vw;
                }
            }
        }
    }

    .col_3 {
        justify-content: left;
        margin: 5rem 0;

        .wrapper {
            position: relative;
            width: 40vw;

            .img {
                display: flex;
                justify-content: space-between;
                align-items: flex-end;

                img {
                    position: absolute;
                    width: 30vw;
                    height: 40vw;
                }

                .right_wing {
                    font-family: "Louis George Cafe Light";
                    color: white;
                    font-size: 2vw;
                    margin: 0;
                    writing-mode: vertical-rl;
                    z-index: 1;
                    width: 35vw;
                }
            }
        }
    }
    .col_4 {
        justify-content: right;
        margin: 10vw 0;

        .wrapper {
            position: relative;
            width: 40vw;

            .img {
                display: flex;
                justify-content: left;
                align-items: flex-end;

                img {
                    position: absolute;
                    width: 30vw;
                    height: 40vw;
                }

                .right_wing {
                    font-family: "Louis George Cafe Light";
                    color: white;
                    font-size: 2vw;
                    margin: 0;
                    writing-mode: vertical-rl;
                    z-index: 1;
                    width: 35vw;
                }
            }
        }
    }
}
