.Web {
    .Webkit {
        width: 100%;
        background-color: tomato;
        margin-top: 20rem;
        .title_box {
            width: 90%;
            margin-bottom: 5rem;

            p:nth-child(1) {
                font-family: "vision";
                font-weight: 900;
                font-size: 3vw;
                color: #f2f4f6;
                margin: 3rem 0;
            }

            p {
                width: 50%;
                height: auto;
                font-size: 1.5vw;
                color: white;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: flex-start;
                font-family: "Louis George Cafe Light";
                line-height: 2vw;
                margin: 0;
            }
        }

        .img_boxes {
            width: 100%;
            height: 100%;
            margin-bottom: 5rem;

            img {
                width: 100%;
                object-fit: cover;
            }
        }
    }
}

.Webkit2 {
    .img_boxes2 {
        width: 100%;
        margin: 0;

        display: flex;
        justify-content: space-around;
        align-items: center;

        img {
            width: 100%;
            object-fit: cover;
        }
    }
}

